        if (r.ErrorCode == 9999) {
          _this29.dialogPush.shopList = [];
          r.Data.Results.forEach(function (e) {
            e.loading = false;
            e.checked = false;
            e.selectCollection = "";
            e.collections = [];
            e.disabled = false;

            _this29.dialogPush.shopList.push(e);
          });
          _this29.dialogPush.shopList = _this29.groupBy(_this29.dialogPush.shopList);
          _this29.dialogPush.isShow = true;
        } else {
          _this29.$elementMessage(r.Message, "error");
