    },
    groupBy: function groupBy(arr) {
      var map = {},
          dest = [];

      for (var i = 0; i < arr.length; i++) {
        var ai = arr[i];

        if (!map[ai.platform]) {
          //
          dest.push({
            platform: ai.platform,
            //
            data: [ai]
          });
          map[ai.platform] = ai; //
        } else {
          for (var j = 0; j < dest.length; j++) {
            var dj = dest[j];

            if (dj.platform == ai.platform) {
              //
              dj.data.push(ai);
              break;
            }
          }
        }
      }

      return dest;
    },
    singPush: function singPush(item) {
      var items = [];
      item.pushRe = [];
      items.push(item);
      this.pushItem(items);
    },
    batItems: function batItems(type) {
      //
      var items = [];
      this.items.forEach(function (e) {
        if (e.checked) {
          e.pushRe = [];
          items.push(e);
        }
      });

      if (!items.length) {
        this.$elementMessage("Please select the products", "error");
        return;
      }

      var ids = [];
      items.forEach(function (e) {
        ids.push(e.id);
      }); // console.log(items)

      switch (type) {
        case "push":
          this.pushItem(items);
          break;

        case "export":
          this.expItem(ids);
          break;

        case "del":
          this.delItem(ids);
          break;

        case "addTags":
          this.itemsAddTags(ids);
          break;
      }
    },
    changeAllItems: function changeAllItems(event) {
      var _this30 = this;

      // 
      this.items.forEach(function (e) {
        if (e.status == '1') _this30.$set(e, "checked", event);
      });
    },
    importPorduct: function importPorduct() {
      var _this31 = this;

      //
      if (!this.importUrl) {
        this.$elementMessage("URL required", "error");
        return;
      }

      this.loadingImport = true;
      this.$Burying({
        object: '8006',
        objectUrl: this.importUrl
      });
      this.$apiCall("api.Product.importPorduct", {
        url: this.importUrl
      }, function (r) {
        _this31.loadingImport = false;

        if (r.ErrorCode == 9999) {
          _this31.importUrl = "";

          _this31.$elementMessage("Import success", "success");

          _this31.toPage(1);
        } else {
          _this31.$elementMessage(r.Message, "error");
        }
      });
    },
    getItem: function getItem(s) {
      var _this32 = this;

      //
      this.loading = true;
      var params = {
        page: this.page,
        rowsPerPage: this.rowsPerPage,
        tagIds: [],
        accountIds: []
      };
      this.tags.forEach(function (tag) {
        if (tag.fChecked) {
          params.tagIds.push(tag.id);
        }
      });
      this.stores.forEach(function (store) {
        if (store.fChecked) {
          params.accountIds.push(store.id);
        }
      });
      this.$apiCall("api.Product.findImportProduct", params, function (r) {
        _this32.loading = false;

        if (r.ErrorCode == 9999) {
          _this32.items = [];
          r.Data.Results.forEach(function (e) {
            e.imgsJson = JSON.parse(e.imgsJson);
            e.checked = false;
            e.tagsVisible = false;

            _this32.items.push(e);
          });
          _this32.total = Number(r.Data.Pagination.totalCount);
          _this32.totalPage = Number(r.Data.Pagination.totalPage); //

          _this32.filterNum = 0;
          _this32.$root.$children[0].$refs.mainScroll.wrap.scrollTop = 0;
          if (params.tagIds.length) _this32.filterNum++;
          if (params.accountIds.length) _this32.filterNum++;
        } else {
          _this32.$elementMessage(r.Message, "error");
        }
      });
      this.getImportTask();
    },
    clearFilter: function clearFilter() {
      var _this33 = this;

      //
      this.tags.forEach(function (tag) {
        _this33.$set(tag, "fChecked", false);
      });
      this.stores.forEach(function (store) {
        _this33.$set(store, "fChecked", false);
      });

      if (this.$route.query.page == 1) {
        this.getItem();
      } else {
        this.$router.push({
          query: {
            page: 1
          }
        });
      }

      this.$refs.filterDropdown.hide();
    },
    filterGetItem: function filterGetItem() {
      //
      if (this.$route.query.page == 1) {
        this.getItem();
      } else {
        this.$router.push({
          query: {
            page: 1
          }
        });
      }

      this.$refs.filterDropdown.hide();
    },
    toPage: function toPage(val) {
      if (val != this.$route.query.page) {
        this.$router.push({
          query: {
            page: val
          }
        });
      } else {
        this.getItem();
      }
    },
    changePageSize: function changePageSize(val) {
      this.rowsPerPage = val;
      localStorage.setItem("c_importPerPage", val);
      this.getItem();
    },
    gotoPage: function gotoPage() {
      this.page = this.$route.query.page ? this.$route.query.page : 1;
      this.getItem();
    }
  }
};